<template>
    <a-spin :spinning="loading">
      <a-form-model
        ref="form"
        :model="entity"
        :rules="rules"
        autocomplete="off"
        layout="vertical"
      >
        <div class="card">
          <a-row :gutter="24">
            <a-col :sm="6">
              <!-- 提示信息 -->
              <a-form-model-item
                label="报废分类名称"
                prop="scrapCategoryName"
                ref="scrapCategoryName"
              >
                <a-input
                  v-model="entity.scrapCategoryName"
                  placeholder="请输入报废分类名称"
                  name="scrapCategoryName"
                  @change="inChange"
                ></a-input>
              </a-form-model-item>
            </a-col>
            <a-col :sm="12">
              <a-form-model-item label="描述" prop="description">
                <a-textarea
                  v-model:value="entity.description"
                  placeholder="描述"
                  :rows="4"
                  :max-length="1000"
                  name="description"
                />
              </a-form-model-item>
            </a-col>
          </a-row>
        </div>
        <!-- 报废原因表格 -->
         <g-collapse headerName="报废原因">
            <template #content>
                <a-table :columns="columns" :data-source="reasons" bordered rowKey="reasonId">
                    <template slot="reasonName" slot-scope="text, record">
                      <a-input v-model:value="record.reasonName" />
                    </template>
                    <template slot="enabled" slot-scope="text, record">
                      <a-switch v-model:checked="record.enabled" />
                    </template>
                    <template slot="remark" slot-scope="text, record">
                      <a-input v-model:value="record.remark" />
                    </template>
                    <template slot="operation" slot-scope="text, record">
                      <a-space>
                        <a-button type="primary" size="small" @click="editReason(record)">
                          编辑
                        </a-button>
                        <a-popconfirm
                          title="确定删除吗?"
                          @confirm="deleteReason(record.reasonId)"
                        >
                          <a-button type="danger" size="small">
                            删除
                          </a-button>
                        </a-popconfirm>
                      </a-space>
                    </template>
                </a-table>
                <a-space>
                  <a-button type="primary" @click="addReason">新增原因</a-button>
                  <a-button type="danger" @click="batchDeleteReasons">批量删除</a-button>
                </a-space>
            </template>
         </g-collapse>
        <g-collapse headerName="测试">
          <template #content>
            <a-row :gutter="24">
              <a-col :sm="6">
                <!-- 创建人 -->
                <span>admin</span>
              </a-col>
              <a-col :sm="6">
                <!-- 最近修改时间 -->
                <span> 2025-04-20 15:00</span>
              </a-col>
            </a-row>
          </template>
        </g-collapse>
      </a-form-model>
    </a-spin>
  </template>
  
  <script>
  import { GNdoPageEditBase } from "@/utils";
  export default {
    name: "CreateOrEditScrapCategory",
    mixins: [GNdoPageEditBase],
    data() {
      return {
        entity: {
          scrapCategoryName: "",
        },
        rules: {
          scrapCategoryName: [
            { required: true, message: "不能为空", trigger: "change" },
            {
              pattern: /^[^\s]+(\s+[^\s]+)*$/,
              trigger: "change",
              message: "报废分类名称不能包含空格",
            },
          ],
        },
         // 报废原因表格列定义
         columns: [
          {
            title: "原因ID",
            dataIndex: "reasonId",
            key: "reasonId",
            // hidden: true, // 隐藏列
          },
          {
            title: "原因名称",
            dataIndex: "reasonName",
            key: "reasonName",
            width: '30%',
            align: 'center',
            scopedSlots: {
              customRender: 'reasonName'
            }
          },
          {
            title: "是否启用",
            dataIndex: "enabled",
            key: "enabled",
            width: '20%',
            align: 'center',
            scopedSlots: {
              customRender: 'enabled'
            }
          },
          {
            title: "备注",
            dataIndex: "remark",
            key: "remark",
            align: 'center',
            scopedSlots: {
              customRender: 'remark'
            }
          },
          {
            title: '操作',
            dataIndex: 'operation',
            key: 'operation',
            width: '20%',
            align: 'center',
            scopedSlots: {
              customRender: 'operation'
            }
          },
        ],
        reasons: [
            {
                reasonId: 1,
                reasonName: "原因1",
                enabled: true,
                remark: "备注1",
            },
            
        ],
      };
    },
  
    mounted() {},
  
    methods: {
      // 初始化
      init() {
        console.log(this.id, "初始化");
        this.loading = true;
        if (this.id) {
          // 模拟请求
          setTimeout(() => {
            this.loading = false;
  
            this.entity = {
              scrapCategoryName: "测试",
              description: "描述",
            };
          }, 500);
        } else {
          this.reset();
          this.loading = false;
        }
      },
      resetFormData() {
        // 重置表单绑定的数据源
        this.entity = {
          scrapCategoryName: "",
        };
      },
      editReason(record) {
        console.log("编辑原因", record);
        // 复制一份 record，避免直接修改原始数据
        this.currentReason = { ...record };
        this.editModalVisible = true;
      },
      addReason() {
        console.log("新增原因");
        // 添加一个新对象到 reasons 数组
        this.reasons.push({
          reasonId: this.reasons.length + 1,
          reasonName: "",
          enabled: true,
          remark: "",
        });
      },
      batchDeleteReasons() {
        console.log("批量删除原因");
        // 删除所有选中的原因
        this.reasons = this.reasons.filter(reason => !reason.selected);
      },
      deleteReason(reasonId) {
        console.log("删除原因", reasonId);
        // 删除指定原因
        this.reasons = this.reasons.filter(reason => reason.reasonId !== reasonId);
      },
      async save() {
        // 保存 校验
        let res = await this.checkError();
        if (res) {
          this.loading = true;
          setTimeout(() => {
            this.loading = false;
            this.saveSuccess(this.entity.scrapCategoryName);
            this.handleSaveEndBackView(this.id);
            this.refreshParent({
              reset: null,
            });
          });
        }
      },
  
      inChange(e) {
        console.log(e.target.value);
      },
    },
  };
  </script>
  
  <style lang="scss" scoped></style>